.table-row {
    display: flex;
}

.table-row:not(#table-headers) {
    min-height: calc(2.5vw + 30px);
}

.table-column {
    padding: 5px;
    flex: 1 1;
    font-size: .85vw;
}

#table-headers {
    padding: 3px 0px;
    border-radius: 5px 5px 0px 0px;
    border-bottom: 1px solid #b8b7b7;
}

#table-headers .table-column {
