.table-column {
    padding: 10px;
}

.table-column .countries {
    display: flex;
    flex-wrap: wrap;
    -webkit-animation: fadeIn 0.5s ease-in;
            animation: fadeIn 0.5s ease-in;
}

.table-column .countries:hover {
